.button {
  min-height: 40px;
  text-align: center;
  line-height: 150%;
  padding: 0 30px;
  background-color: #00A4E5;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 1px;
  transition: all 0.3s ease 0s;

  &:hover {
    cursor: pointer;
    opacity: 0.8;
  }
}
